.vacancies {
  background-color: $black-light2;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  overflow: hidden;
  min-height: 300px;
  &__title {
    position: relative;
    z-index: 3;
  }
}

.vacancy {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  color: $white;
  margin-bottom: 30px;
  z-index: 2;
  @include tabletL {
    flex-direction: column;
  }
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: -73rem;
    overflow: hidden;
    width: 3500px;
    height: 150px;
    background: $black-light;
    transform: rotate(30deg);
    z-index: -1;
    opacity: .2;
    @include tabletL {
      top: 80%;
    }
  }
  &:after {
    content: '';
    position: absolute;
    top: 10%;
    left: -83rem;
    height: 60px;
    width: 3500px;
    overflow: hidden;
    transform: rotate(30deg);
    background: $accent;
    z-index: -1;
    opacity: .5;
    @include tabletL {
      top: 80%;
    }
  }
  &__aside {
    font-weight: 300;
    font-size: 24px;
    margin-right: 10px;
    color: $accent;
    @include tabletL {
      margin-bottom: 50px;
    }
  }
  &__position {
    font-weight: bold;
    color: $white;
  }
  &__main {
    max-width: 900px;
    @include laptopM {
      max-width: 630px;
    }
  }
  &__title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
    color: $accent;
  }
  &__descr {
    position: relative;
    font-size: 18px;
    margin-bottom: 50px;
    padding-right: 50px;
    z-index: 3;
  }
  &__list {
    font-size: 18px;
    margin-bottom: 50px;
  }
  &__item {
    &:before {
      content: '-';
    }
  }
  &__link {
    color: $accent;
  }
}