.card {
  background: $second;
  padding: 20px;
  &-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    @include tabletL {
      flex-direction: column;
      align-items: flex-start;
    }
    &__left {
      display: flex;
      @include tabletL {
        margin-bottom: 20px;
      }
    }
  }
  &__title {
    text-transform: uppercase;
    text-align: left;
    font-size: 20px;
    @include mobileL {
      font-size: 15px;
    }
  }
  &-info {
    display: inline-flex;
    align-items: center;
    font-size: 24px;
    font-weight: 700;
    margin-right: 10px;
    @include tablet {
      font-size: 20px;
      justify-content: flex-start;
    }
    @include mobileL {
      font-size: 15px;
    }
    &__price {
      color: $accent;     
    }
  }
  &-choosen {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 60px;
    &__btn {
      background: none;
      & svg {
        pointer-events: none;
      }
      @include mobileL {
        & svg {
          width: 25px;
          height: 25px;
        }
      }
      &:hover svg {
        fill: $active;
      }
    }
  }
  &__wrapper {
    display: flex;
    justify-content: space-between;
    @include tabletL {
      align-items: center;
      flex-direction: column;
    }
  }
  &-block {
    padding: 15px;
    margin-bottom: 15px;
  }
  &-main {
    width: 100%;
    max-width: 66%;
    display: flex;
    flex-direction: column;
    @include tabletL {
      max-width: 100%;
    }
  }
  &-images {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    &__block {
      &-top {
        position: relative;
        width: 420px;
        margin-bottom: 10px;
        @include tablet {
          width: initial;
        }
      }
    }
    &__link {
      position: relative;
      display: block;
      transition: .3s opacity;
      &:hover:after {
        opacity: .5;
      }
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: $accent;
        opacity: 0;
      }
    }
    &__img {
      display: block;
      max-width: 100%;
      height: 150px;
      width: 150px;
      object-fit: cover;
      &--main {
        width: auto;
        height: auto;
      }
    }
    &__thumbnails {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      &-block {
        padding: 10px;
        @include mobileL {
          padding: 5px;
        }
      }
      &-btn {
        width: 100%;
      }
    }
    &__btn {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 70px;
      height: 70px;
      @include tabletS {
        width: 40px;
        height: 40px;
      }
    }
  }
  &-content {
    &__object-container {
      background: $second-light;
    }
    &__object {
      width: 100%;
      display: flex;
      justify-content: space-between;
      @include tabletM {
        flex-direction: column;
      }
      &-col {
        width: 50%;
        margin-right: 10px;
        &:last-child {
          margin-right: 0;
        }
        @include tablet {
          margin-right: 0;
          width: 100%;
        }
      }
      &-row {
        width: 100%;
        font-weight: 700;
        margin-bottom: 10px;
        font-size: 18px;
        @include tablet {
          width: 100%;
          font-size: 16px;
        }
        & span {
          font-weight: 400;
        }
      }
    }
    &__descr {
      background: $white;
    }
    &__title {
      position: relative;
      font-size: 18px;
      margin-bottom: 15px;
      @include mobileL {
        font-size: 14px;
      }
    }
    &__text {
      font-size: 18px;
    }
  }
  &-similar {
    &__container {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
    }
    &__block {
      position: relative;
      margin-bottom: 10px;
      max-width: 1000px;
      width: 48%;
      @include tablet {
        width: 100%;
        padding: 0 10px;
      }
    }
  }
  &-aside {
    max-width: 33%;
    width: 100%;
    @include tabletL {
      max-width: 100%;
    }
  }
  &-map {
    border: 1px solid $black;
    background: $white;
    width: 100%;
    height: 488px;
  }
  &-rieltor {
    max-width: 210px;
    margin: 0 auto;
    background: $white;
  }
  &-client {
    max-width: 210px;
    margin: 0 auto;
    text-align: center;
    color: $white;
    background: $accent;
    &__name {
      margin-bottom: 5px;
    }
    &__link {
      color: $white;
    }
  }
}

.card-edit, .card-new {
  background: $second;
  &__title {
    margin-bottom: 30px;
    font-size: 18px;
    color: $accent;
  }
}