//colors
$black: #000;
$black-light: lighten($black, 10%);
$black-light2: lighten($black, 70%);
$black-dark: lighten($black, 6%);
$white: #fff;
$accent: #1F5A9C;
$accent-light: lighten($accent, 40%);
$second: #E8E8E8;
$second-light: #c7c7c7;
$second-dark: #696969;
$active: #ffd700;
$alert: red;
$sub: #B7FFC8;
$shadow-down: 0px 10px 15px -3px rgba(0,0,0,0.5);
$shadow: 0px 0px 18px 0px $white;
$shadow-dark: 0px 7px 11px 0px $black;
$text-shadow: 1px 1px 2px $black, 0 0 1px $white;
$gradient: linear-gradient(to bottom, rgba(31,89,156,1) 0%, rgba(120,116,212,1) 100%);
$accent-gradient: linear-gradient(to bottom, rgba(255,255,255,0.45) 0%, rgba(31,89,156,0.49) 53%, rgba(255,255,255,0.52) 100%);
$gradient-left: linear-gradient(to right, rgba(31,89,156,1) 0%, rgba(255,255,255,0.4) 76%, rgba(255,255,255,0.21) 100%);

//header
$header-height: 144px;
$header-height-mini: 117px;