.property-cards-filter {
  margin: 0 auto 20px auto;
  padding: 15px;
  border: 2px solid $accent;
  background: $white;
  @include mobileL {
    padding: 5px;
    margin-top: -4px;
    margin-bottom: 20px;
    background: $white;
  }
  &__buttons {
    display: flex;
    align-items: center;
    justify-content: space-around;
    @include mobileL {
      font-size: 14px;
    }
  }
  &__sort {
    font-size: 20px;
    text-transform: uppercase;
    color: $accent;
    font-weight: 700;
    @include tabletS {
      display: none;
      font-size: 14px;
    }
  }
  &__btn {
    @include mobileL {
      padding: 3px;
      font-size: 12px;
    }
  }
}