.news-block {
	width: 100%;
	&__block {
		width: 100%;
		padding: 20px;
		&--border {
			border: 2px solid $black;
		}
	}
	&__preview {
		text-align: center;
		&-title {
			font-size: 26px;
			margin-bottom: 20px;
			@include laptop {
				font-size: 18px;
			}
		}
		&-container {
			padding: 20px;
			display: flex;
			justify-content: space-around;
			flex-wrap: wrap;
			@include tablet {
				align-items: center;
			}
			@include mobileL {
				flex-direction: column;
			}
		}
		&-block {
			margin-right: 10px;
			margin-bottom: 20px;
			&:last-child {
				margin-right: 0;
			}
			@include tablet {
				margin-right: 0;
			}
		}
	}
}