.news-article {
	width: 100%; 
	margin-bottom: 20px;
  &--preview {
		max-width: 600px;
  }
  &__container {
  	display: flex;
  	justify-content: space-around;
  	@include tablet {
			flex-direction: column;
			align-items: center;
		}
  }
	&__main {
		&-link {
			display: block;
		}
	}
	&__img {
    width: 500px;
		max-width: 100%;
		&-wrapper {
			width: 100%;
			text-align: center;
			margin-bottom: 20px;
		}
	}
	&__primal {
		text-align: left;
		&-title {
			font-size: 20px;
			margin-bottom: 5px;
			@include laptop {
				font-size: 17px;
			}
		}
		&-sub-title {
			font-size: 17px;
			margin-bottom: 10px;
			@include laptop {
				font-size: 15px;
			}
		}
		&-text {
			font-size: 15px;
			@include laptop {
				font-size: 14px;
			}
		}
	}
	&__date {
		font-size: 14px;
		margin-bottom: 10px;
	}
}