.body-fixed {
  overflow: hidden;
}

.active-block {
	display: block;
}

.active-flex {
	display: flex;
}

.active-btn {
	background: $white;
	color: $accent;
}

.active-btn-br{
	border-radius: 15px;
	background: $accent;
	color: $white;
}

.active-color {
	color: $accent;
}

.display-none {
	display: none;
}

.hidden-block {
	position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  visibility: hidden;
  opacity: 0;
}