.registration {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - #{$header-height});
  @include tablet {
  	min-height: calc(100vh - #{$header-height-mini});
  }
	&-menu {
		margin-bottom: 30px;
		text-align: center;
		&__link {
