.services {
  &--c {
    background: $second;
  }
  &__container {
    width: 100%;
    margin-bottom: 33px;
  }
  &__block {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto 20px auto;
    padding-bottom: 10px;
    border-bottom: 2px solid $accent;
    &-title {
      margin-bottom: 15px;
      text-align: center;
      font-size: 20px;
      font-weight: 700;
      color: $accent;
      @include tabletL {
        font-size: 16px;
      }
    }
  }
  &__col {
    width: 50%;
    text-align: center;
  }
  &__list {
    font-size: 18px;
    margin-bottom: 12px;
    &--accent {
      color: $accent;
      font-weight: 700;
    }
    &--num {
      list-style: decimal;
      font-weight: 700;
      color: $accent;
      & li {
        &:before {
          content: none;
        }
      }
    }
  }
  &__item {
    position: relative;
    margin-bottom: 10px;
    margin-left: 25px;
    font-weight: 700;
    &:before {
      content: '';
      position: absolute;
      top: 0.6em;
      left: -20px;
      width: 5px;
      height: 5px;
      border-radius: 50%;
      background: $accent;
    }
  }
  &__text {
    font-size: 17px;
    margin-bottom: 7px;
    @include tabletL {
      font-size: 15px;
    }
    @include mobileL {
      font-size: 14px;
    }
  }
  &__text-title {
    font-size: 18px;
    text-align: center;
    font-weight: 700;
    margin-bottom: 10px;
  }
  &__name {
    font-weight: 700;
  }
  &__price {
    font-weight: 400;
  }
  &__btn {
    margin: 0 auto 13px auto;
    font-size: 17px;
    padding: 15px;
  }
}