.news-text-preview {
	&__menu {
		text-align: center;
		margin-bottom: 0;
		&-title {
			font-size: 26px;
			margin-bottom: 20px;
			@include laptop {
				font-size: 18px;
			}
		}
	}
	&__list {
		margin-bottom: 30px;
	}
	&__item {
		margin-bottom: 15px;
	}
	&__link {
		font-size: 18px;
		display: block;
		@include laptop {
			font-size: 16px;
		}
		@include laptop {
			font-size: 14px;
		}
	}
	&__btn {
		margin: 0 auto;
		font-size: 22px;
		padding: 20px;
		@include laptop {
			font-size: 18px;
		}
		&-wrapper {
			width: 100%;
			text-align: center;
		}
	}
}