.header {
	position: fixed;
	display: flex;
	justify-content: center;
	flex-direction: column;
  max-width: 1920px;
	width: 100%;
	background-color: $white;
	box-shadow: $shadow-down; 
	z-index: 200;
	@include laptopL {
		max-width: 1440px;
	}
	@include tabletL {
		justify-content: space-between;
	}
	&__wrapper {
		position: relative;
		height: $header-height;
		@include tabletL {
			height: $header-height-mini;
		}
	}
	&-top {
		display: flex;
		justify-content: space-between;
		background: $accent;
		padding: 5px 15px;
		color: $white;
		@include tabletL {
			display: none;
		}
		&__contacts-info {
			margin-right: 10px;
		}
	}
	&-bottom {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 10px 15px;
	}
	&__logo {
		text-align: center;
		&:hover .header__logo-name {
			color: $accent;
			transition: color .5s;
		}
		&-link {
			display: block;
		}
		&-img {
			width: 78px;
			margin-bottom: 5px;
		}
		&-name {
			white-space: nowrap;
			text-transform: uppercase;
			font-size: 14px;
			font-weight: bold;
			transition: color .5s;
		}
	}
	&__contacts {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		white-space: nowrap;
    font-size: 16px;
    margin-right: 30px;
    @include tabletL {
    	display: none;
    }
    &-info {
    	display: flex;
    	align-items: center;
    	margin-bottom: 2px;
    }
    &-icon {
    	margin-right: 5px;
    }
    &-link {
    	color: $accent;
    }
	}
	&__nav-wrapper {
		width: 100%;
		@include tabletL {
			display: none;
		}
	}
	&__nav {
		width: 100%;
		max-width: 560px;
		font-size: 16px;
		text-transform: uppercase;
		white-space: nowrap;
		@include laptopM {
			font-size: 14px;
		}
	}
	&__list {
		display: flex;
		justify-content: flex-start;
		align-items: center;
	}
	&__item {
		position: relative;
		margin-right: 20px;
		&:last-child {
			margin-right: 0;
		}
		&:hover>.header__dropdown {
			display: block;
		}
	}
	&__link {
		cursor: pointer;
		transition: color 0.5s;
		&:hover {
			color: $accent;
		}
	}
	&__menu {
		display: flex;
		position: relative;
		margin-right: 15px;
		@include tabletL {
			height: 100%;
			margin-right: 0;
		}
		&-call {
			display: none;
			@include tabletL {
				display: block;
			}
			&-link {
				position: relative;
				display: block;
				height: 100%;
				padding: 30px;
				@include mobileL {
					padding: 15px;
				}
				& p {
					position: relative;
					top: 50%;
					transform: translateY(-50%);
					transition: transform .5s;
					&:hover {
						transform: translateY(-50%) rotate(45deg);
					}
				}
			}
			&-dropdown {
				display: none;
		    position: absolute;
		    top: 100%;
		    right: 50%;
		    padding: 10px;
		    background: $white;
		    border: 2px solid $accent;
		    min-width: 166px;
				&-item {
					margin-bottom: 20px;
					&:last-child {
						margin-bottom: 0;
					}
				}
				&-link {
					color: $accent;
				}
			}
		}
		&-wrapper {
			@include tabletL {
				width: 100px;
			}
			@include mobileL {
				width: 70px;
			}
		}
		&-registration {
			height: 100%;
		}
		&-lang {
			height: 10%;
			text-align: center;
		}
		&-select {
			background-color: transparent;
			text-transform: uppercase;
			border: none;
			color: $white;
			& option {
				color: $black;
			}
			&--dark {
				color: $black;
			}
		}
	}
	&__btn {
		width: 100%;
		height: 100%;
		padding: 10px;
		& svg {
			margin-right: 5px;
		}
		&-registration {
			@include tabletL {
				display: none;
			}
		}
		&-menu {
			position: relative;
			display: none;
			transition: rotate .5s;
			&:hover .header__btn-menu-line {
				background-color: $accent;
			}
			@include tabletL {
				display: block;
			}
			&-active {
				span:nth-child(1) {
					top: 50%;
					transform: translateX(-50%) rotate(-45deg);
				}
				span:nth-child(3) {
					top: 50%;
					transform: translateX(-50%) rotate(45deg);
				}
				span:nth-child(2) {
					display: none;
				}
			}
			&-line {
				content: "";
				width: 80%;
				height: 2px;
				background-color: $second;
				position: absolute;
				left: 50%;
		    transform: translateX(-50%);
				transition: 0.3s;
				&:nth-child(1) {
			    top: 25%;
				}
				&:nth-child(2) {
			    top: 50%;
				}
				&:nth-child(3) {
			    top: 75%;
				}
			}
		}
	}
	&__dropdown {
		display: none;
		position: absolute;
		top: 100%;
		left: 0;
		padding: 5px;
		background: $white;
		border: 2px solid $accent;
		&-item {
			margin-bottom: 10px;
			&:last-child {
				margin-bottom: 0;
			}
		}
		&-link {
			position: relative;
			transition: border 0.5s;
			&:before {
		    content: '';
		    position: absolute;
		    bottom: 0;
		    right: 0;
		    width: 0;
		    height: 5px;
		    background-color: $accent;
		    transition: 1s;
		    opacity: .6;
		    z-index: 0;
			}
			&:hover:before {
		    width: 100%;
  			left: 0;
			}
		}
	}
}

.header__mobile {
	display: none;
	position: absolute;
	top: 100%;
	left: 0;
	background: $second;
	min-height: calc(106vh - #{$header-height});
	width: 100%;
	height: 100%;
	border: 2px solid $black;
	overflow-y: auto;
	padding-bottom: 1px;
	&-active {
		display: block;
	}
	&-nav {
		padding-top: 30px;
		margin-bottom: 20px;
		text-transform: uppercase;
		text-align: center;
		&-list {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}
		&-item {
	    width: 90%;
	    padding: 5px;
			margin-bottom: 20px;
			border-bottom: 2px solid $accent;
		}
		&-link {
			&:hover {
				& svg path {
					fill: $accent;
					transition: fill .5s;
				}
			}
		}
	}
	&-lang {
		margin-bottom: 20px;
	}
	&-btn {
		padding: 40px;
	}
	&-dropdown {
		margin-top: 10px;
		display: none;
		text-align: center;
		&-item {
			margin-bottom: 10px;
			&:last-child {
				margin-bottom: 0;
			}
		}
		&-link {
			position: relative;
		}
	}
	&-contacts {
		display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    &-info {
    	margin-bottom: 10px;
    	&:last-child {
    		margin-bottom: 0;
    	}
    }
	}
}