.my-form {
  margin-bottom: 15px;
  &__wrapper {
    margin: 0 auto;
    min-width: 260px;
  }
  &__sub-title {
    margin-bottom: 15px;
    font-size: 24px;
  }
  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 380px;
    margin: 0 auto;
  }
  &__block {
    margin-bottom: 10px;
    text-align: center;
    width: 100%;
    & input[type="file"] {
      background: #ffffff !important;
      color: $black;
      font-size: 20px;
      @include mobileL {
        font-size: 15px;
      }
    }
  }
  &__input-wrapper {
    position: relative;
    margin-bottom: 15px;
    text-align: center;
    width: 100%;
  }
  &__text {
    font-size: 18px;
    color: $accent;
    font-weight: 700;
    margin-bottom: 15px;
    text-align: center;
  }
  &__label {
    background: $white;
    position: absolute;
    top: -9px;
    left: 5%;
    padding: 0 10px;
    font-size: 16px;
    color: $accent;
    text-transform: uppercase;
    white-space: nowrap;
    @include mobileL {
      font-size: 14px;
    }
  }
  &__input {
    width: 100%;
    padding: 11px;
    border: 2px solid $accent-light;
    border-bottom: 2px solid $sub;
    text-align: center;
    font-weight: 200;
    font-size: 16px;
    &:focus {
      border: 2px solid $accent;
    }
    @include mobileL {
      font-size: 14px;
      padding: 8px;
    }
    @include input-placeholder {
      font-weight: 200;
      color: $black;
    }
    &--popup {
      font-size: 18px;
      @include mobileL {
        font-size: 16px;
        padding: 10px;
      }
    }
  }
  &__textarea {
    border: 2px solid $accent-light;
    border-bottom: 2px solid $sub;
    max-height: 100px;
    min-height: 100%;
    height: 100px;
    width: 100%;
    max-width: 500px;
    min-width: 100%;
    color: $black;
    font-size: 20px;
    text-align: left;
    padding: 10px;
    &:focus {
      border: 2px solid $accent;
    }
    @include mobileL {
      font-size: 18px;
    }
  }
  &__email {
    &-text {
      margin-bottom: 30px;
    }
  }
  &__personal-data {
    &-block {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 30px;
    }
    &-checkbox {
      position: relative;
      padding: 0;
      height: initial;
      width: initial;
      margin-bottom: 0;
      cursor: pointer;
    }
    &-label {
      position: relative;
      cursor: pointer;
      color: $black;
      margin-left: 37px;
      @include mobileL {
        font-size: 14px;
      }
    }
    &-link {
      font-weight: 700;
      &:hover {
        color: $accent;
      }
    } 
  }
  &__btns {
    width: 100%;
    display: inline-flex;
  }
  &__btn {
    padding: 8px;
    font-size: 17px;
    width: 100%;
    margin-right: 10px;
    &:last-child {
      margin-right: 0;
    }
    @include mobileL {
      font-size: 14px;
    }
    &--popup {
      font-size: 18px;
      @include mobileL {
        font-size: 16px;
        padding: 10px;
      }
    }
  }
  &__success {
    font-size: 20px;
    margin-bottom: 20px;
    text-align: center;
    @include mobileL {
      font-size: 15px;
    }
  }
  &__psw-text {
    &:hover {
      color: $accent;
    }
  }
}