.footer {
	background: $black-light;
	&__container {
		padding: 20px;
		position: relative;
	}
	&__title {
		color: $white;
		margin-bottom: 30px;
	}
	&-main {
		display: flex;
    justify-content: space-between;
    align-items: center;
    @include tabletL {
      flex-direction: column;
      justify-content: center;
    }
    &__block {
    	margin-right: 10px;
    	&:last-child {
    		margin-right: 0;
    	}
    	@include tabletL {
    		margin-right: 0;
    	}
    }
		&__btn {
	    padding: 12px;
	    font-size: 20px;
      margin-bottom: 20px;
	    @include laptop {
		    font-size: 18px;
	    }
	    @include tabletL {
	    	font-size: 16px;
	    }
	    @include mobileL {
	    	font-size: 14px;
	    }
		}
	}
	&__contacts {
		&-text {
			display: flex;
	    align-items: center;
	    justify-content: flex-start;
	    margin-bottom: 15px;
	    font-size: 16px;	    
		}
		&-icons {
			margin-right: 50px;
		}
		&-info {
			color: $white;
		}
		&-link {
			color: $white;
		}
	}
	&__logo {
		margin-right: 20px;
		@include tabletL {
			margin-right: 0;
			margin-bottom: 20px;
		}
		&-img {
			max-width: 100%;
			width: 300px;
			@include laptopM {
				width: 200px;
			}
			@include tablet {
				width: 130px;
			}
		}
	}
	&__map {
		position: relative;
		text-align: center;
		width: 100%;
		max-width: 400px;
		min-height: 48px;
		padding: 8px;
		border: 2px solid $accent;
		&-img {
			max-width: 100%;
			opacity: 0.8;
			vertical-align: middle;
			@include tabletL {
				height: initial;
			}
		}
		&-btn {
			position: absolute;
			bottom: 0;
			right: 0;
		}
	}	
	&__social {
		display: block;
		&-link {
			display: inline-block;
			margin-right: 20px;
			&:last-child {
				margin-right: 0;
			}
		}
		&-icon {
			svg {
				transition: fill .5s;
				&:hover {
					fill: $accent;
				}
			}
		}
	}	
	&__copyright {
		@include tabletS {
			text-align: center;
		}
	}	
	&-bottom {
		display: flex;
		justify-content: space-between;
		align-items: center;
		background: $black;
		color: $white;
		padding: 20px;
		@include tabletS {
			flex-direction: column;
		}
		&__block {
			@include tabletS {
				margin-bottom: 20px;
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}
}