body {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  color: $black;
  overflow-x: hidden;
}

a {
  color: $black;
}

.glider-dot {
  background: $second;
  color: $second;
  &.active {
    background: $accent;
    color: $accent;
  }
}

.container {
  position: relative;
  max-width: 1920px;
  margin: 0 auto;
  @include laptop {
    max-width: 1440px;
  }
}

.container-inner {
  position: relative;
  max-width: 1170px;
  margin: 0 auto;
}

.container-mini {
  max-width: 900px;
  margin: 0 auto;
}

.section {
  padding: 15px 20px;
  @include tabletS {
    padding: 10px 6px;
  }
}

.title {
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  font-size: 24px;
  color: $black;
  margin-bottom: 15px;
  white-space: nowrap;
  line-height: 1.2;
  & span {
    display: block;
  }
  &--light {
    font-weight: 300;
  }
  &--left {
    text-align: left;
  }
  &--white {
    color: $white;
  }
  &--accent {
    color: $accent;
  }
  @include tabletL {
    font-size: 20px;
  }
  @include mobileL {
    font-size: 18px;
  }
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  background: $accent;
  border: 2px solid $accent;
  padding: 10px;
  white-space: nowrap;
  text-transform: uppercase;
  color: $white;
  text-align: center;
  transition: all .3s;
  outline: none;
  cursor: pointer;
  line-height: 1.2;
  &--hover {
    &:hover {
      background: $white;
      border: 2px solid $accent;
      color: $accent;
      & svg {
        fill: $accent;
      }
    }
  }
  &--invert {
    color: $accent;
    background: $white;
    &:hover {
      background: $accent;
      border: 2px solid $accent;
      color: $white;
    }
  }
  &--delete {
    background: #fff;
    color: red;
    border-color: red;
  }
  &--text {
    background: $white;
    border: 2px solid $white;
    color: $accent;
  }
  &--area {
    & svg {
      margin-right: 20px;
      transition: .5s all;
    }
    &:hover svg {
      fill: $accent;
    }
    @include mobileL {
      & svg {
        margin-right: 13px;
      }
    }
  }
  &--br {
    border-radius: 30px;
  }
  &--sq {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 45px;
    height: 45px;
    padding: 0;
    font-size: 20px;
    background: $accent;
    @include mobileL {
      width: 30px;
      height: 30px;
      font-size: 18px;
    }
  }
  &--mix {
    display: inline-flex;
    background: transparent;
    color: $accent;
    border: none;
    &:hover {
      & .btn--mix__sq {
        background: $white;
        color: $accent;
        border: 2px solid $accent;
      }
    }
    &__sq {
      margin-right: 10px;
      color: $white;
      transition: all .3s;
    }
  }
  &--border {
    background: transparent;
    border: 2px solid $accent;
    color: $black;
  }
  &--shadow {
    box-shadow: $shadow-dark;
  }
  &--simple {
    text-transform: none;
  }
}