.main-menu {
  display: block;
  width: 100%;
  height: calc(100vh - #{$header-height});
  max-height: 950px;
  @include tabletL {
    max-height: initial;
    height: initial;
  }
  &__nav {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    @include tabletL {
      flex-direction: column;
    }
  }
  &__bg {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    @include tabletL {
      height: 50vh;
    }
  }
  &__col {
    width: 100%;
    height: 100%;
  }
  &__col-inner {
    display: flex;
    height: 50%;
    @include tabletL {
      flex-direction: column;
      height: 50vh;
    }
    &--max {
      height: 100%;
      @include tabletL {
        flex-direction: column;
        height: 50vh;
      }
    }
    &--mini {
      @include tabletL {
        flex-direction: column;
        height: 100%;
      }
    }
  }
  &__col-row {
    height: 100%;
    width: 100%;
    @include tabletL {
      height: 50vh;
    }
  }
  &__link {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    &:hover .main-menu__inner {
      background: $accent;
    }
  }
  &__inner {
    background: $black;
    opacity: 0.5;
    width: 100%;
    height: 100px;  
    text-align: center;
    transition: background 1s;
    @include tabletL {
      height: 70px;
    }
    &--max {
      height: 150px;
      @include tabletL {
        height: 90px;
      }
    }
    &--mini {
      height: 70px; 
    }
  }
  &__name {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 24px;
    color: $white;
    text-align: center;
    text-transform: uppercase;
    white-space: nowrap;
    @include tabletL {
      font-size: 20px;
    }
    &--mini {
      font-size: 20px;
    }
    &--normal {
      white-space: normal;
    }
  }
  &__line {
    margin: 10px auto;
    height: 2px;
    width: 100%;
    background: $white;
  }
}