.experts-block {
  &__slider {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }
}

.experts-card {
  &__container {
    margin-right: 20px;
    &:last-child {
      margin-right: 0;
    }
  }
  &__img-wrapper {
    width: 100%;
  }
  &__img {
    max-width: 100%;
    width: 100%;
    object-fit: cover;
    display: block;
  }
  &__body {
    padding: 5px;
    text-align: center;
    background-color: $accent;
    border: 1px solid $black;
    word-wrap: break-word;
    color: $white;
    min-height: 86px;
    & a {
      display: block;
      color: $white;
    }
  }
  &__title {
    margin-bottom: 5px;
    vertical-align: middle;
    @include laptop {
      font-size: 18px;
    }
  }
  &__position {

  }
}