.pagination {
	max-width: 300px;
	margin: 0 auto;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	&__link {
	  color: $black;
	  padding: 10px;
	  transition: background-color .3s;
	  &--active {
	  	background-color: $accent;
  		color: white;
	  }
	  &:hover:not(.pagination__link--active) {
	  	background-color: $second;
	  }
	  &-wrapper {
			width: 100%;
			padding: 10px;
			margin-bottom: 12px;
			background: $accent-light;
			display: flex;
			justify-content: center;
			align-items: center;
		}
	}
	&__btn {
		width: 100%;
		padding: 10px;
		font-size: 18px;
		&-wrapper {
			width: 100%;
		}
	}	
}