.searchform {
  & button {
    @extend .btn;
    @extend .btn--hover;
    margin: 0 auto 30px auto;
  }
  & textarea {
    max-width: 100%;
  }
}

.searchform .form-group {
  width: 30%;
  max-width: 30%;
  @include tablet {
    max-width: 100%;
    width: 100%;
  }
}

.form-group {
  position: relative;
  text-align: center;
  font-size: 16px;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 15px;
  color: $black;
}

.control-label {
  background: $white;
  position: absolute;
  top: -11px;
  right: 5%;
  border: 1px solid $accent;
  padding: 0 10px;
  font-size: 14px;
  color: $accent;
  white-space: nowrap;
  z-index: 2;
  @include mobileL {
    padding: 0 5px;
  }
}

.form-control {
  width: 100%;
  height: 100%;
  font-size: 16px;
  padding: 14px;
  border: 1px solid $accent;
  border-bottom: 2px solid $sub;
  background: $white;
}

.form-group select{
  @extend .form-control;
  text-transform: uppercase;
  & option:checked{
    color: $accent;
  }
}

.field-object-image {
  padding: 11px;
  @extend .form-control;
}

.field-object, .field-searchform {
  &-square {
    @extend .form-group;
  }
  &-squaremin {
    @extend .form-group;
  }
  &-squaremax {
    @extend .form-group;
  }
  &-livingsquare {
    @extend .form-group;
  }
  &-floor {
    @extend .form-group;
  }
  &-floors {
    @extend .form-group;
  }
  &-rooms {
    @extend .form-group;
  }
  &-house {
    @extend .form-group;
  }
  &-parking {
    @extend .form-group;
  }
  &-landarea {
    @extend .form-group;
  }
  &-metrostation {
    @extend .form-group;
  }

  &-image, &-title, &-type, &-price, &-currency, &-type, &-market {
  }

  &-type, &-market {
    
  }
}

.field-object-market, .field-object-type, .field-object-parking,
.field-searchform-market, .field-searchform-type, .field-searchform-parking {
  & .control-label {
    display: block;
    right: 50%;
    transform: translateX(50%);
  }
}

.params, .other {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  @include tablet {
    flex-direction: column;
  }
  & span {
    width: 100%;
    font-size: 15px;
    margin-bottom: 14px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    color: $accent;
    display: inline-block;
    margin-bottom: 20px;
  }
}

.other, .hidden {
  display: none;
}

#object-type, #object-market, #object-parking,
#searchform-type, #searchform-market, #searchform-parking {
  display: flex;
  & label {
    font-size: 14px;
    @extend .btn;
    width: 100%;
    margin-right: 10px;
    &:last-child {
      margin-right: 0;
    }
    &:hover {
      @extend .btn--hover;
    }
  }
  & input {
    margin-right: 5px;
  }
}

#searchform-type, #searchform-market {
  @include tabletL {
    flex-wrap: wrap;
    & label { 
      margin: 0;
    }
  }
}

.display-other {
  @extend .btn--hover;
}