.feedback-popup {
	display: none;
	position: fixed;
	align-items: center;
  justify-content: center;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100%;
	z-index: 1111;
	padding: 10px;
	&__wrapper {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: $black;
		opacity: .5;
	}
	&__container {
		position: relative;
		background: $white;
		width: 100%;
		max-width: 400px;
		margin: 0 auto;
		border: 2px solid $black;
		@include MHtabletM {
    	overflow: auto;
		}
	}
	&__input {
		font-size: 16px;
		padding: 12px;
	}
	&__btn {
		position: fixed;
		bottom: 8%;
		right: 10px;
		width: 50px;
		height: 50px;
		border-radius: 50%;
		padding: 5px;
    line-height: 0;
    z-index: 2222;
    opacity: 1;
    background: $white;
    & svg {
    	fill: $accent;
    }
    &:hover {
    	background: $accent;
    	& svg {
    		fill: $white;
    	}
    }
	}
	&__btn-close {
		position: absolute;
		top: 10px;
		right: 10px;
		font-size: 24px;
		@include tablet {
			font-size: 14px;
		}
	}
}