.info {
  display: flex;
  justify-content: space-around;
  width: 100%;
  background-image: url(../img/about/about.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &__block {
    flex: 50%;
    display: flex;
    @include tabletL {
      padding: 15px;
    }
    &--first {
      justify-content: flex-end;
      margin-right: 20px;
      @include tabletL {
        margin-right: 0;
        justify-content: center;
        align-items: center;
      }
    }
    &--last {
      justify-content: flex-start;
      @include tabletL {
        justify-content: center;
        align-items: center;
      }
    }
  }
  &__block-wrapper {
    max-width: 500px;
    margin: 40px 0;
    @include tabletL {
      margin: 0;
    }
    &--first {
      background: $white;
      border-left: 6px solid $accent;
      box-shadow: $shadow;
    }
  }
  @include tabletL {
    flex-direction: column;
  }
}

.about {
  position: relative;
  height: 100%;
  @include tabletM {
    background-size: cover;
  }
  &__text {
    margin-bottom: 20px;
    margin-right: 20px;
  }
  &__text-title {
    margin-bottom: 10px;
  }
  &__descr {
    color: $black;
    font-size: 18px;
    text-align: left;
    max-width: 750px;
    margin: 0 auto 20px auto;
    @include mobileL {
      font-size: 14px;
    }
  }
  &__btn {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 35px;
    height: 35px;
  }
}

.statistics {
  display: flex;
  height: 100%;
  &__title {
    margin-bottom: 15px;
  }
  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    width: 100%;
    margin-bottom: 15px;
  }
  &__block {
    padding: 10px;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    font-size: 15px;
    margin-bottom: 10px;
    width: 540px;
    color: $white;
    background: $gradient-left;
    & svg {
      fill: $white;
    }
    @include laptopM {
      width: 450px;
    }
    @include tabletM {
      width: 100%;
      font-size: 14px;
      flex-direction: column;
      text-align: center;
      & svg {
        width: 10px;
        height: 10px;
      }
    }
    &:hover {
      background: $accent;
      color: $white;
      box-shadow: $shadow;
      & svg {
        fill: $white;
      }
    }
  }
  &__counter {
    width: 70%;
    @include mobileL {
      width: 100%;
    }
  }
  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border: 1px solid $white;
    border-radius: 50%;
    margin-right: 30px;
    @include tabletM {
      height: 40px;
      width: 40px;
      margin-right: 0;
      margin-bottom: 10px;
    }
    & svg {
      width: 25px;
      height: 25px;
    }
  }
}

.activity {
  display: none;
  &__title {
    white-space: normal;
  }
  &__list {
    font-size: 16px;
  }
  &__item {
    position: relative;
    margin-bottom: 10px;
    margin-left: 20px;
    &:before {
      content: '';
      position: absolute;
      top: 0.6em;
      left: -20px;
      width: 5px;
      height: 5px;
      border-radius: 50%;
      background: $accent;
    }
  }
  &__text {
    text-transform: uppercase;
    font-size: 18px;
    color: $accent;
    text-align: center;
  }
}

.team {
  padding: 30px 40px;
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 25px;
    @include tabletL {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
  &__block {
    &--img {
      flex: 20%;
      text-align: center;
      @include tabletL {
        margin-bottom: 30px;
      }
    }
    &--text {
      flex: 80%;
      text-align: right;
      @include tabletL {
        text-align: center;
      }
    }
  }
  &__person {
    position: relative;
    margin-bottom: 50px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: -20px;
      width: 3px;
      height: 100%;
      background: $accent;
    }
    @include tabletL {
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: -20px;
        width: 3px;
        height: 100%;
        background: $accent;
      }
    }
  }
  &__name {
    font-size: 24px;
    margin-bottom: 20px;
  }
  &__position { 
    font-weight: 300;
    font-size: 16px;
  }
  &__bio {
    font-size: 18px;
    margin-left: 100px;
    @include tabletL {
      margin-left: 0;
    }
  }
}

.certificates {
  background: $accent-gradient;
  &__container {
    padding: 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 auto;
    @include tablet {
      flex-direction: column;
      justify-content: center;
    }
  }
  &__block {
    position: relative;
    width: 190px;
    margin: 0 40px 45px 0;
    transition: all .5s;
    &:before, &:after {
      content: '';
      position: absolute;
      height: 60px;
      width: 60px;
      transition: all .5s;
    }
    &:before {
      top: -5%;
      right: 80%;
      border-top: 2px solid $accent;
      border-left: 2px solid $accent;
    }
    &:after {
      bottom: -5%;
      left: 80%;
      border-bottom: 2px solid $accent;
      border-right: 2px solid $accent;
    }
    &:hover:before {
      transform: translate(-20%);
    }
    &:hover:after {
      transform: translate(20%);
    }
    @include laptopM {
      width: 200px;
      &:before, &:after {
        width: 50px;
      }
    }
    @include tablet {
      margin: 0;
      margin-bottom: 40px;
    }
  }
  &__btn {
    position: absolute;
    right: 0;
    bottom: 1%;
    z-index: 20;
  }
  &__img {   
    max-width: 100%;    
  }
}

.partners {
  background-image: url(../img/partners/partners-bg.jpg);
  background-repeat: no-repeat;
  background-position: 20% 70%;
  background-size: cover;
  &__images {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 auto 20px auto;
    @include tablet {
      flex-direction: column;
      justify-content: center;
    }
  }
  &__block {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 400px;
    max-height: 200px;
    padding: 10px 20px;
  }
  &__img {
    max-width: 100%;
    @include tablet {
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.news-home-page {
  width: 100%;
  &__title {
    margin-bottom: 20px;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
    @include tabletL {
      flex-direction: column;
      margin-bottom: 20px;
    }
  }
  &__block {
    margin-right: 20px;
    &:last-child {
      margin-right: 0;
    }
    @include tabletL {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }
}